.home-styles-container {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.notifications-container {
  width: 100%;
  margin-block: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.notifications {
  height: 100%;
  background-color: #324f5d;
  padding-block: 10px;
  padding-inline: 15px;
  border-radius: 20px;
  color: antiquewhite;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.notifications label {
  font-size: 30px;
}

.cash-display-container {
  width: 100%;
  margin-block: 20px;
  background-color: #324f5d;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-block: 20px;
  border-radius: 20px;
  color: antiquewhite;
}

.cash-display-container .title {
  width: 90%;
  text-align: start;
}

.cash-display-container .amount {
  width: 90%;
  height: 50%;
  text-align: center;
  font-size: 40px;
}

@media (max-width: 768px) {
  .home-styles-container {
    width: 100%;
    height: 100%;
  }

  .notifications-container {
    flex-direction: column;
    width: 90%;
    gap: 10px;
    margin: 0px;
  }

  .notifications {
    width: 100%;
    padding: 0px;
  }
}
